'use client';

import { useMemo, useState } from 'react';
import { motion } from 'framer-motion';
import { Building2, Clock3, Plus, Trash2 } from 'lucide-react';
import clsx from 'clsx';

const statusStyles = {
  Planned: 'bg-slate-500/20 text-slate-200 border border-slate-500/40',
  'In Progress': 'bg-sky-500/20 text-sky-200 border border-sky-400/50',
  'At Risk': 'bg-amber-500/20 text-amber-100 border border-amber-300/40',
  Completed: 'bg-emerald-500/20 text-emerald-100 border border-emerald-300/40',
};

const gradients = ['from-sky-500/30', 'from-amber-500/30', 'from-emerald-500/30', 'from-rose-500/30'];

const formatDate = (value) => {
  if (!value) return 'No deadline';
  return new Intl.DateTimeFormat('en', { month: 'short', day: 'numeric' }).format(new Date(value));
};

export default function ProjectDashboard({
  projects = [],
  selectedProjectId,
  onSelect = () => {},
  onCreate = async () => {},
  onDelete = () => {},
  onProgressUpdate = async () => {},
  busyProjectIds = new Set(),
}) {
  const [form, setForm] = useState({
    name: '',
    studio: 'Core Studio',
    dueDate: '',
    color: '#38bdf8',
  });
  const [progressDrafts, setProgressDrafts] = useState({});
  const [submitting, setSubmitting] = useState(false);

  const totals = useMemo(() => {
    if (!projects.length) {
      return { average: 0, active: 0, completed: 0 };
    }
    const completed = projects.filter((p) => p.status === 'Completed').length;
    const active = projects.length - completed;
    const average = Math.round(
      projects.reduce((acc, project) => acc + (project.progress || 0), 0) / projects.length,
    );

    return { average, active, completed };
  }, [projects]);

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!form.name.trim()) return;
    try {
      setSubmitting(true);
      await onCreate(form);
      setForm({
        name: '',
        studio: 'Core Studio',
        dueDate: '',
        color: '#38bdf8',
      });
    } finally {
      setSubmitting(false);
    }
  };

  const handleProgressChange = (projectId, value) => {
    setProgressDrafts((prev) => ({
      ...prev,
      [projectId]: value,
    }));
  };

  const commitProgress = async (projectId) => {
    const value = progressDrafts[projectId];
    if (value === undefined) return;
    try {
      await onProgressUpdate(projectId, value);
    } finally {
      setProgressDrafts((prev) => {
        const copy = { ...prev };
        delete copy[projectId];
        return copy;
      });
    }
  };

  const renderProjectCard = (project, index) => {
    const isSelected = project._id === selectedProjectId;
    const cardProgress = progressDrafts[project._id] ?? project.progress ?? 0;
    const busy = busyProjectIds?.has?.(project._id) ?? false;

    return (
      <motion.div
        key={project._id}
        role="button"
        tabIndex={0}
        onClick={() => onSelect(project._id)}
        onKeyDown={(event) => {
          if (event.key === 'Enter' || event.key === ' ') {
            event.preventDefault();
            onSelect(project._id);
          }
        }}
        className={clsx(
          'relative w-full rounded-2xl border border-white/5 p-4 text-left transition-all',
          'bg-white/5 hover:bg-white/10 focus-visible:outline focus-visible:outline-2 focus-visible:outline-sky-400',
          isSelected ? 'ring-2 ring-sky-400/60 shadow-glass' : 'ring-0',
        )}
        whileHover={{ y: -2, scale: 1.01 }}
        transition={{ type: 'spring', stiffness: 300, damping: 24 }}
      >
        <div
          className={clsx(
            'absolute inset-0 -z-10 rounded-2xl bg-gradient-to-br to-transparent opacity-70 blur-2xl',
            gradients[index % gradients.length],
          )}
        />
        <div className="flex items-start justify-between">
          <div>
            <p className="text-sm uppercase tracking-widest text-slate-400">Project</p>
            <h3 className="text-xl font-semibold text-white">{project.name}</h3>
            <div className="mt-1 flex items-center gap-2 text-xs text-slate-400">
              <span
                className="inline-block h-2.5 w-2.5 rounded-full"
                style={{ backgroundColor: project.color || '#38bdf8' }}
              />
              <span>{project.studio || 'Studio'}</span>
            </div>
          </div>
          <span
            className={clsx(
              'rounded-full px-3 py-1 text-xs font-semibold tracking-wide',
              statusStyles[project.status] || statusStyles['In Progress'],
            )}
          >
            {project.status}
          </span>
        </div>

        <div className="mt-4 space-y-3">
          <div className="flex items-center gap-2 text-sm text-slate-300">
            <Clock3 size={16} />
            <span>{formatDate(project.dueDate)}</span>
          </div>
          <div className="space-y-1">
            <div className="flex items-center justify-between text-xs uppercase tracking-wide text-slate-400">
              <span>Progress</span>
              <span className="font-semibold text-white">{cardProgress}%</span>
            </div>
            <div className="h-2.5 rounded-full bg-white/10">
              <div
                className="h-full rounded-full bg-gradient-to-r from-sky-400 via-cyan-400 to-emerald-400 transition-all"
                style={{ width: `${cardProgress}%` }}
              />
            </div>
            <input
              type="range"
              min={0}
              max={100}
              value={cardProgress}
              onChange={(event) => handleProgressChange(project._id, Number(event.target.value))}
              onMouseUp={() => commitProgress(project._id)}
              onTouchEnd={() => commitProgress(project._id)}
              onKeyUp={(event) => {
                if (event.key === 'Enter' || event.key === ' ') {
                  commitProgress(project._id);
                }
              }}
              className="mt-2 w-full accent-sky-400"
            />
          </div>
          <div className="flex items-center justify-between text-xs text-slate-300">
            <span>
              {project.taskSummary?.completed || 0}/{project.taskSummary?.total || 0} tasks done
            </span>
            <button
              type="button"
              onClick={(event) => {
                event.stopPropagation();
                onDelete(project._id);
              }}
              className="inline-flex items-center gap-1 rounded-full border border-white/10 px-2 py-1 text-[11px] text-slate-300 hover:border-rose-400/60 hover:text-rose-200"
            >
              <Trash2 size={12} /> Remove
            </button>
          </div>
          {busy && (
            <p className="text-xs text-sky-200/80">Syncing progress…</p>
          )}
        </div>
      </motion.div>
    );
  };

  return (
    <section className="glass-panel rounded-3xl p-6 shadow-glass">
      <div className="flex items-center gap-3">
        <div className="rounded-2xl bg-sky-500/20 p-3 text-sky-300">
          <Building2 size={24} />
        </div>
        <div>
          <p className="text-sm uppercase tracking-[0.2em] text-slate-400">Studios</p>
          <h2 className="text-2xl font-semibold text-white">Project Dashboard</h2>
        </div>
      </div>

      <div className="mt-6 grid gap-4 sm:grid-cols-3">
        <StatPill label="Active" value={totals.active} accent="text-sky-300" />
        <StatPill label="Completed" value={totals.completed} accent="text-emerald-300" />
        <StatPill label="Avg. Progress" value={`${totals.average}%`} accent="text-amber-200" />
      </div>

      <div className="mt-6 space-y-4">
        {projects.length ? (
          projects.map((project, index) => renderProjectCard(project, index))
        ) : (
          <p className="rounded-2xl border border-dashed border-white/20 p-6 text-center text-slate-400">
            No projects yet. Add your first studio brief below.
          </p>
        )}
      </div>

      <form
        id="project-create"
        onSubmit={handleSubmit}
        className="mt-6 space-y-3 rounded-2xl border border-white/10 bg-white/5 p-4"
      >
        <div className="flex items-center gap-2 text-sm text-slate-300">
          <Plus size={16} /> <span>New project</span>
        </div>
        <div className="grid gap-3 sm:grid-cols-2">
          <label className="space-y-1 text-sm text-slate-300">
            Name
            <input
              className="w-full rounded-xl border border-white/10 bg-studio-ink/60 px-3 py-2 text-white focus:border-sky-400/60 focus:outline-none"
              placeholder="Expo Pavilion"
              value={form.name}
              onChange={(event) => setForm((prev) => ({ ...prev, name: event.target.value }))}
            />
          </label>
          <label className="space-y-1 text-sm text-slate-300">
            Studio
            <input
              className="w-full rounded-xl border border-white/10 bg-studio-ink/60 px-3 py-2 text-white focus:border-sky-400/60 focus:outline-none"
              placeholder="Core Studio"
              value={form.studio}
              onChange={(event) => setForm((prev) => ({ ...prev, studio: event.target.value }))}
            />
          </label>
          <label className="space-y-1 text-sm text-slate-300">
            Due date
            <input
              type="date"
              className="w-full rounded-xl border border-white/10 bg-studio-ink/60 px-3 py-2 text-white focus:border-sky-400/60 focus:outline-none"
              value={form.dueDate}
              onChange={(event) => setForm((prev) => ({ ...prev, dueDate: event.target.value }))}
            />
          </label>
          <label className="space-y-1 text-sm text-slate-300">
            Accent color
            <input
              type="color"
              className="h-10 w-full cursor-pointer rounded-xl border border-white/10 bg-studio-ink/60"
              value={form.color}
              onChange={(event) => setForm((prev) => ({ ...prev, color: event.target.value }))}
            />
          </label>
        </div>
        <button
          type="submit"
          disabled={submitting}
          className="inline-flex w-full items-center justify-center gap-2 rounded-xl bg-gradient-to-r from-sky-500 to-cyan-400 px-4 py-2 font-semibold text-slate-900 transition hover:opacity-95 disabled:cursor-not-allowed disabled:opacity-60"
        >
          <Plus size={16} />
          {submitting ? 'Creating…' : 'Add Project'}
        </button>
      </form>
    </section>
  );
}

function StatPill({ label, value, accent }) {
  return (
    <div className="rounded-2xl border border-white/5 bg-white/5 p-4">
      <p className="text-xs uppercase tracking-[0.3em] text-slate-400">{label}</p>
      <p className={clsx('mt-2 text-2xl font-semibold', accent)}>{value}</p>
    </div>
  );
}
