'use client';

import { useEffect, useMemo, useState } from 'react';
import { motion } from 'framer-motion';
import { Building2 } from 'lucide-react';
import ProjectDashboard from '../components/ProjectDashboard';
import TaskPanel from '../components/TaskPanel';
import TeamOverview from '../components/TeamOverview';
import { api } from '../lib/api';

const normalizeProject = (project) => ({
  ...project,
  taskSummary: project.taskSummary ?? { total: 0, completed: 0 },
});

export default function HomePage() {
  const [projects, setProjects] = useState([]);
  const [teamMembers, setTeamMembers] = useState([]);
  const [selectedProjectId, setSelectedProjectId] = useState(null);
  const [loading, setLoading] = useState(true);
  const [projectSyncing, setProjectSyncing] = useState(() => new Set());

  useEffect(() => {
    let cancelled = false;

    const bootstrap = async () => {
      setLoading(true);
      try {
        const [projectsResponse, teamResponse] = await Promise.all([
          api.getProjects(),
          api.getTeamMembers(),
        ]);
        if (!cancelled) {
          setProjects(projectsResponse.map(normalizeProject));
          setTeamMembers(teamResponse);
          if (projectsResponse.length) {
            setSelectedProjectId(projectsResponse[0]._id);
          }
        }
      } catch (error) {
        console.error('Failed to load workspace data', error);
      } finally {
        if (!cancelled) {
          setLoading(false);
        }
      }
    };

    bootstrap();
    return () => {
      cancelled = true;
    };
  }, []);

  const selectedProject = useMemo(() => {
    if (!projects.length) {
      return null;
    }
    return projects.find((project) => project._id === selectedProjectId) ?? projects[0];
  }, [projects, selectedProjectId]);

  const handleProjectCreate = async (payload) => {
    const project = await api.createProject(payload);
    const normalized = normalizeProject(project);
    setProjects((prev) => [normalized, ...prev]);
    setSelectedProjectId(normalized._id);
  };

  const handleProjectDelete = async (projectId) => {
    const project = projects.find((item) => item._id === projectId);
    if (!project) return;
    if (!window.confirm(`Delete ${project.name}? Tasks for this project will also be removed.`)) {
      return;
    }
    await api.deleteProject(projectId);
    const wasSelected = selectedProjectId === projectId;
    setProjects((prev) => {
      const next = prev.filter((item) => item._id !== projectId);
      if (wasSelected) {
        setSelectedProjectId(next[0]?._id ?? null);
      }
      return next;
    });
  };

  const handleProgressUpdate = async (projectId, progressValue) => {
    setProjectSyncing((prev) => {
      const next = new Set(prev);
      next.add(projectId);
      return next;
    });
    try {
      const updated = await api.updateProjectProgress(projectId, progressValue);
      setProjects((prev) =>
        prev.map((project) =>
          project._id === projectId ? normalizeProject({ ...project, ...updated }) : project,
        ),
      );
    } finally {
      setProjectSyncing((prev) => {
        const next = new Set(prev);
        next.delete(projectId);
        return next;
      });
    }
  };

  const handleProjectHydrate = (updatedProject) => {
    if (!updatedProject) return;
    setProjects((prev) =>
      prev.map((project) => (project._id === updatedProject._id ? normalizeProject(updatedProject) : project)),
    );
  };

  const refreshTeam = async () => {
    const list = await api.getTeamMembers();
    setTeamMembers(list);
  };

  const handleTeamCreate = async (payload) => {
    await api.createTeamMember(payload);
    await refreshTeam();
  };

  if (loading) {
    return (
      <main className="flex min-h-screen items-center justify-center text-slate-400">
        <div className="space-y-2 text-center">
          <p className="text-sm uppercase tracking-[0.3em]">Loading studio</p>
          <div className="h-1 w-40 overflow-hidden rounded-full bg-white/10">
            <div className="h-full w-1/2 animate-pulse rounded-full bg-sky-400" />
          </div>
        </div>
      </main>
    );
  }

  return (
    <main className="mx-auto max-w-6xl space-y-8 px-4 py-8">
      <motion.header
        initial={{ opacity: 0, y: -12 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.4 }}
        className="glass-panel relative overflow-hidden rounded-3xl p-8 shadow-glass"
      >
        <div className="absolute inset-0 -z-10 bg-grid bg-[size:24px_24px]" />
        <div className="flex flex-wrap items-center gap-3 text-sm text-slate-400">
          <Building2 size={20} className="text-sky-300" />
          <span>VZNX Studio Workspace</span>
        </div>
        <div className="mt-4 flex flex-wrap items-end justify-between gap-4">
          <div>
            <h1 className="text-3xl font-semibold text-white sm:text-4xl">
              Focused cockpit for architecture teams
            </h1>
            <p className="mt-2 max-w-2xl text-slate-300">
              Track briefs, nudge deliverables, and balance the team’s workload without juggling multiple tools.
            </p>
          </div>
          <div className="rounded-2xl border border-white/10 bg-white/5 px-5 py-4 text-right">
            <p className="text-sm uppercase tracking-[0.3em] text-slate-500">Teams</p>
            <p className="text-2xl font-semibold text-white">{teamMembers.length}</p>
          </div>
        </div>
      </motion.header>

      <section className="grid gap-6 lg:grid-cols-[1.1fr_0.9fr]">
        <ProjectDashboard
          projects={projects}
          selectedProjectId={selectedProjectId}
          onSelect={setSelectedProjectId}
          onCreate={handleProjectCreate}
          onDelete={handleProjectDelete}
          onProgressUpdate={handleProgressUpdate}
          busyProjectIds={projectSyncing}
        />
        <TaskPanel
          project={selectedProject}
          teamMembers={teamMembers}
          onProjectUpdate={handleProjectHydrate}
          onTeamRefresh={refreshTeam}
        />
      </section>

      <TeamOverview teamMembers={teamMembers} onCreate={handleTeamCreate} />
    </main>
  );
}
